import Login from './components/login';
import App from './components/app';
import Registration from './components/registration';

const routes = [
    {
        component: App,
        href: '/'
    },
    {
        component: Login,
        href: '/login'
    },
    {
        component: Registration,
        href: '/registration'
    }
];

export default routes;


